.sigma_btn-custom {
  cursor: pointer;
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  position: relative;
  background-color: #f74f22;
  border: 0;
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  padding: 15px 30px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  border-radius: 0px;
  font-weight: 600;
  overflow: hidden;
  z-index: 1;
}

.sigma_btn-custom::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: center;
  background-color: #d43308;
  width: 10px;
  height: calc(100% - 15px);
  z-index: -1;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
}

.sigma_btn-custom:focus::before,
.sigma_btn-custom:hover::before {
  width: calc(100% - 14px);
  opacity: 1;
  visibility: visible;
}

