.sigma_volunteers.volunteers-3 .sigma_volunteers-thumb::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  margin: 10px;
  border-radius: 50%;
  background-color: #022147;
  opacity: 0;
  z-index: 3;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transform: scale(0);
