}
.map-marker > span {
  width: 10px;
  display: block;
  height: 10px;
  background-color: #f74f22;
  border-radius: 50%;
}
.map-marker > img {
  width: 50px;
  border-radius: 50%;
  border: 2px solid #fff;
}
.map-marker-content {
  position: absolute;
  top: -15px;
  right: calc(100% + 15px);
  transition: 0.3s;
  opacity: 0;
  min-width: 350px;
  visibility: hidden;
  padding: 10px;
  background-color: #282828;
  color: #dedede;
  font-size: 12px;
  z-index: 3;
}
.map-marker-content::before {
  content: '';
  position: absolute;
  top: 10px;
  width: 0;
  left: 100%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  height: 0;
  border-top: 10px solid transparent;
  border-right: 10px solid #022147;
  border-bottom: 10px solid transparent;
}
.map-marker.right .map-marker-content::before {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  right: 100%;
  left: auto;
}
.map-marker.right .map-marker-content {
  right: auto;
  left: calc(100% + 15px);
}
.map-marker-content p {
  color: #dedede;
  margin: 0 0 5px;
  font-size: 12px;
}
.map-marker:hover .map-marker-content {
  opacity: 1;
  visibility: visible;
}

.map-marker.marker-1 {
  position: absolute;
  top: 40%;
  right: 40%;
}
.map-marker.marker-2 {
  position: absolute;
  top: 30%;
  right: 20%;
}
.map-marker.marker-3 {
  position: absolute;
  top: 45%;
  right: 50%;
}
.map-marker.marker-4 {
  position: absolute;
  top: 20%;
  right: 85%;
}
.map-marker.marker-5 {
  position: absolute;
  top: 60%;
  right: 70%;
}
.map-marker.marker-6 {
  position: absolute;
  top: 20%;
  right: 30%;
}

